@import "../styles/display";

.expenses-filter {
  color: white;
  padding: 0 1rem;
}

.expenses-filter__control {
  @include displayFlex(space-between, center);
  width: 100%;
  margin: 1rem 0;
}

.expenses-filter {
  label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
}

.expenses-filter {
  select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
  }
}
